@import url(https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: 'Baloo 2', cursive, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* INICIO GLOBAL */
#root{
  height: 100%;
  /* width: 84%; */
}

p{
  margin-bottom: 0px;
  margin-top: 0px;
}

.row{
  margin-left: 0px;
  margin-right: 0px;
}

.col{
  padding-left: 0px;
  padding-right: 0px;
}
.col-sm{
  padding-left: 0px;
  padding-right: 0px;
}
/* FIM GLOBAL */

/* INICIO VARIAVEIS */
* {
  --cor-branca-n1:#ffffff;
  --cor-branca-n2: #f9f9fc;
  --cor-branca-n3: #d5dae5;
  --cor-cinza-n1: #767676;
  --cor-cinza-n2: #767676;
  --cor-preto-n1: #3b4046;
  --cor-verde-n1: #51FA5B;
  --cor-vermelho-n1: #e4292f;
  --cor-amarelo-n1: #F5F545;
  --cor-azul-n1: #33a3f9;
  --cor-background:#f6f5fb;
}
/* FIM VARIAVEIS */


/* INICIO PADRﾃグ */
.btn-padrao{
  color: #ffffff;
  background-color: #f25c5c;
  border-color: #d44444;
  font-weight: 500;
}
.btn-padrao:hover{
  color: #ffffff;
  background-color: #d44444;
  border-color: #f25c5c;
  font-weight: 500;
}

.tooltipo-ajuda{
  height: 25px;
  cursor: pointer;
  margin-left: 20px;
  margin-right: 20px;
} 
/* FIM PADRﾃグ */

